import types from '../types.js';
import axios from 'axios';
import goods from './goods.js';

var state = {
    ratings:[],
    types:['推荐','吐槽','全部'],
    selectRatings:[],
    onlyContent:false,
    selectType:2,

    selectSpecs:[],
    onlySpec:false,
    selectSpec:2,
}

var getters = {
    ratings(state){
        return state.ratings;
    },
    types(state){
        return state.types;
    },
    selectRatings(state){
        return state.selectRatings;
    },
    selectType(state){
        return state.selectType;
    },

    selectSpecs(state){
        return state.selectSpecs;
    },
    selectSpec(state){
        return state.selectSpec;
    },
}

var actions = {
    getRatings({commit,state}){
        // axios.get('/api/ratings').then((res)=>{
        //     // console.log(res.data);
        //     if(res.data.error == 0){
        //         commit( types.GET_RATINGS , res.data.data );
        //     }
        // });
        axios.get('./data.json').then((res)=>{
            // console.log(res.data);
           
                commit( types.GET_RATINGS , res.data.ratings );
            
        });
    },
    change({commit},_types){
        commit(types.CHANGE,_types);
    },
    select({commit},c){
        commit(types.selectTypes,c);
    },
    toggle({commit}){
        commit(types.TOGGLE);
    },




    changeSpec({commit}){
        commit(types.changeSpec);
    },
    selectSpecs({commit},b){
        commit(types.selectSpecs,b);
    },
    toggleSpec({commit}){
        commit(types.toggleSpec);
    },
    
}

var mutations = {
    [types.GET_RATINGS](state,data){
        state.ratings = data;
    },
    [types.CHANGE](state,_types){
        state.types = _types;
        state.selectRatings = state.ratings;
    },
    [types.selectTypes](state,c){
        state.selectType = c;
        state.selectRatings = state.ratings.filter(rating => {
            if(state.selectType == 2){
                return true;
            }else{
                return rating.rateType == state.selectType;
            }
        });
    },
    [types.TOGGLE](state){
        state.onlyContent = !state.onlyContent;
        state.selectRatings = state.ratings.filter(rating => {
            if(state.onlyContent && !rating.text){
                return false;
            }else if(state.selectType == 2){
                return true;
            }else{
                return rating.rateType == state.selectType;
            }
        });
    },




    [types.changeSpec](state){
        state.selectSpecs = goods.state.speratings;
    },
    [types.selectSpecs](state,b){
        state.selectSpec = b;
        state.selectSpecs =  goods.state.speratings.filter(ratinged => {
            if(state.selectSpec == 2){
                return true;
            }else{
                return ratinged.rateType == state.selectSpec;
            }
        });
    },
    [types.toggleSpec](state){
        state.onlySpec = !state.onlySpec;
        state.selectSpecs =  goods.state.speratings.filter(ratinged => {
            if(state.onlySpec && !ratinged.text){
                return false;
            }else if(state.selectSpec == 2){
                return true;
            }else{
                return ratinged.rateType == state.selectSpec;
            }
        });
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


