<template>
<div class="content">
    <div class="left" ref='left'>
        <ul>
            <li v-for='(item,i) in goods' :key="i" class="type" @click="selectMenu(i)" :class="{'left-bg' : i === currentIndex}">
                <span class="text">{{item.name}}</span>
            </li>
        </ul>
    </div>
    <div class="right" ref='right'>
        <div>
            <ul v-for='(item,i) in goods' :key="i" class="right-foods">
                <li class="title">{{item.name}}</li>
                <ul  v-for='(items,j) in item.foods' class="type" :key="j">
                    <li class="goods">
                        <div>
                            <img  :src="items.icon" alt="" @click="showSpec(items)">
                        </div>
                        <div class="info">
                            <p class="named">{{items.name}}</p>
                            <p class="description" v-if="items.description">{{items.description}}</p>
                            <p class="sellCount">月售{{items.sellCount}}份 好评率{{items.rating}}％</p>   
                            <p class="price">¥{{items.price}}</p>
                            <numbers :food ="items"></numbers>
                        </div>
                    </li>
                </ul>
            </ul>
        </div>
    </div>
     
</div>
</template>

<script>
import BScroll from 'better-scroll';

import {mapGetters,mapActions} from 'vuex';
import numbers from "../numbers/numbers.vue";

    export default{
        components:{
            numbers
        },
        data(){
            return {
                listHeight: [],
                scrollY: 0,
            }
        },

        computed:{
            ...mapGetters([
				'goods'
            ]),
            // 计算当前索引值
			currentIndex(){
                // 循环listHeight数组
				for( let i = 0 ; i < this.listHeight.length ; i++ ){
					let height1 = this.listHeight[i];		
					let height2 = this.listHeight[i+1];		
					if( this.scrollY >= height1 && this.scrollY  < height2 ){
						return i;
					}
				}
				return 0 ;
			},
            
        },
        created(){
            this.$store.dispatch('getGoods');
        },
        watch:{
            goods(){
                this.$nextTick(()=>{
                    this.menuBS(); 
                    this.calcHeight();
                });
            }
        },
        methods:{
            ...mapActions([
                'showSpec'
            ]),
             showSpec(items){
                this.$store.dispatch("showSpec",items);
            },
            menuBS(){
                this.menuBScroll = new BScroll( this.$refs.left , {click : true} );
                this.menuBScrolled = new BScroll( this.$refs.right , {click : true,probeType:3} );
                // 实时获取y坐标
				this.menuBScrolled.on('scroll' , (pos) => {
					// console.log(pos.y);
					// console.log( Math.abs( Math.ceil(pos.y)));
                    this.scrollY =  Math.abs(Math.round(pos.y)) + 2;
				})
            },
            // 计算li的高度 并且push进listHeight数组当中
			calcHeight(){
				// 获取所有li
                let foodList = this.$refs.right.getElementsByClassName('right-foods');
				// 初始值为0
				let height = 0;
				this.listHeight.push( height );
				// 循环所有li
				for( let i = 0 ; i < foodList.length ; i++){
					height +=  foodList[i].clientHeight;
					this.listHeight.push( height );
                }
			},
            // 左侧点击事件
			selectMenu(i){
				let foodList = this.$refs.right.getElementsByClassName('right-foods');
                let el = foodList[i];
                // console.log(el);
                this.menuBScrolled.scrollToElement( el , 200 );
			},
        }

    }
</script>

<style lang="less" scoped>
.content{
    position: absolute;
    display: flex;
    overflow: hidden;
    width: 100%;
    top: 3.54rem;
    z-index: -1;
    bottom: 1.04rem;
    // border: 1px solid red;
    .left{
        flex: 0 0 1.6rem;
        width: 1.6rem;
        background: #f3f5f7;
        .type{
            width: 1.12rem;
            height: 1.08rem;
            padding: 0 0.24rem;
            display: table;
            &.left-bg{
                color: #FA5555;
                background: rgba(7,17,27,0.1);
            }
            .text{
                display: table-cell;
                font-size: 0.24rem;
                font-weight: 200;
                line-height: 0.28rem;
                vertical-align: middle;
                border-bottom: 1px solid rgba(7,17,27,0.1);
            }
        }
    }
    .right{
        flex:1;
        .title{
            width:100% ;
            height: 0.52rem;
            // border: 0.01rem solid red;
            text-align: left;
            font-size: 0.24rem;
            line-height: 0.52rem;
            padding-left: 0.28rem;
            vertical-align: middle;
            border-left: 0.05rem solid rgb(147,153,159);
            background-color: #f3f5f7;
        }
        .goods{
            padding: 0.36rem 0 0 0.36rem;
            display: -webkit-box;
            height: 1.7rem;
            border-bottom: 0.03rem solid rgba(7, 17, 27, 0.1);
            img{
                width: 1.17rem;
                height: 1.17rem;
                // border: 1px solid blue;
            }
            .info{
                position: fixed;
                width: 5rem;
                height: 1.4rem;
                text-align: left;
                // border: 0.01rem solid red;
                .named{
                    margin-top: 0.05rem;
                    margin-left: 0.15rem;
                    font-size: 0.28rem;
                    color: rgb(7, 17, 27);
                }
                .description{
                    margin-top: 0.17rem;
                    margin-left: 0.15rem;
                    font-size: 0.2rem;
                    width: 4rem;
                    height: 0.2rem;
                    overflow: hidden;
                    color:rgb(147, 153, 159);
                }
                .sellCount{
                    margin-top: 0.17rem;
                    margin-left: 0.14rem;
                    font-size: 0.2rem;
                    color:rgb(147, 153, 159);
                }
                .price{
                    margin-top: 0.17rem;
                    margin-left: 0.16rem;
                    font-size: 0.28rem;
                    color:red;
                    font-weight: normal;
                }

            }
        }
    }
}   
</style>



