import types from '../types.js'
import axios from 'axios';

var state = {
    goods:[],
    foods:{},
    showSpec:false,
    showShopping:false,
    speratings:[]
}

var getters = {
    goods(state){
        return state.goods;
    },
    showSpec(state){
        return state.showSpec;
    },
    foods(state){
        return state.foods;
    },
    hidespec(state){
        return state.hidespec;
    },

    showShopping(state){
        return state.showShopping;
    },
    hideShopping(state){
        return state.hideShopping;
    },

    speratings(state){
        return state.speratings;
    }

}

var actions = {
    getGoods({commit,state}){
        // axios.get('/api/goods').then((res)=>{
        //     // console.log(res.data)
        //     if(res.data.error == 0){
        //         commit( types.GET_GOODS , res.data.data );
        //     }
        // });
        axios.get('./data.json').then((res)=>{
            // console.log(res.data)
           
                commit( types.GET_GOODS , res.data.goods );
            
        });
    },
    showSpec( { commit} ,items){
        commit( types.showSpec ,items);
    },
    hidespec( { commit} ){
        commit( types.hidespec );
    },

    showShopping( { commit} ){
        commit( types.showShopping );
    },
    hideShopping( { commit} ){
        commit( types.hideShopping );
    },

}

var mutations = {
    [types.GET_GOODS]( state , data ){
		state.goods = data;
    },
    [types.showSpec]( state , items ){
        state.showSpec = true;
        state.foods = items;
        state.speratings = items.ratings;
    },
    [types.hidespec]( state ){
		state.showSpec = false;
    },

    [types.showShopping]( state ){
        state.showShopping = true;
    },
    [types.hideShopping]( state ){
        state.showShopping = !state.showShopping;
        
    },

    
}

export default {
    state,
    getters,
    actions,
    mutations
}


