<template>
<transition enter-active-class="animated bounceInRight" leave-active-class="animated bounceOutLeft">
<div class="spec" v-show="showSpec" ref="wrapper">
    <div>
        <div class="up">
            <img :src="foods.image" alt="">
            <p class="name">{{foods.name}}</p>
            <p class="sellCount">月售{{foods.sellCount}}份</p>
            <p class="rating">好评率{{foods.rating}}％</p>
            <p class="price">¥{{foods.price}}</p>
            <div class="add" v-show='!foods.count' @click = 'addFirstnum'>
                <p >加入购物车</p>
            </div>
            <div class="nums" v-show='foods.count'>
                <numbers :food ="foods"></numbers>
            </div>
            <div class="line"></div>
            <p class="tit">商品介绍</p>
            <p class="info">{{foods.info}}</p>
        </div>
        <div class="middle">
            <div class="line-one"></div>
            <div class="appraise" ><!-- v-for="(item,i) in ratings" :key="i" -->
                <div class="ss left">
                    <p @click="select(2)">{{types[2]}}({{speratings.length}})</p>
                </div>
                <div class="ss center">
                    <p @click="select(0)">{{types[0]}}({{goodCount}})</p>
                </div>
                <div class="ss right">
                    <p @click="select(1)">{{types[1]}}({{badCount}})</p>
                </div>
            </div>
            <div class="selected">
                <div class="selectc" :class="{'q':aa == true}" @click="toggle">
                    <i  class="iconfont icon-duihao"></i>
                    <span>只看有内容的评价</span>
                </div>
            </div>
            <div class="comments">
                <ul>
                    <li v-for='(items,j) in selectSpecs' :key="j">
                        <div class="con">
                            <p class="rateTime">{{items.rateTime | number}}</p>
                            <i :class="{'cc':!items.text}" class="iconfont icon-damuzhion"></i>
                            <p class="text">{{items.text}}</p>
                            <p class="username">{{items.username}}</p>
                            <img :src="items.avatar" alt="">
                        </div>
                    </li>
                </ul>
            </div>

        </div>
        <div class="pass">
            <i @click='hidespec' class="iconfont icon-shiliangzhinengduixiang4"></i>
        </div>
    </div>
    
</div>
</transition>
</template>

<script>
import BScroll from 'better-scroll';
import {mapGetters,mapActions} from 'vuex';
import animate from 'animate.css';
import Vue from 'vue';
import numbers from '../numbers/numbers.vue';

    export default{
        computed:{ 
			...mapGetters([
                'showSpec',
                'foods',
                'speratings',
                'types',
                'selectSpecs',
            ]),
            goodCount(){
                return this.speratings.filter( rating => {
                    return rating.rateType === 0; 
                }).length;
            },
            badCount(){
                return this.speratings.filter( rating => {
                    return rating.rateType === 1; 
                }).length;
            },
           
        },
        data(){
            return {aa:false};
        },
        create(){
            this.$store.dispatch('changeSpec');
        },
        components:{
			numbers
		},
        watch:{
            foods(){
                this.$nextTick(()=>{
                    if(!this.menuBScroll){
                        this.menuBScroll = new BScroll( this.$refs.wrapper , {click:true} );
                    }else{
                        this.menuBScroll.refresh();
                    }
                    
                });
            }
        },
        methods:{
            hidespec(){
                this.$store.dispatch('hidespec');
            },
            addFirstnum(){
                Vue.set( this.foods , 'count' , 1 );
            },
            select(b){
                this.$store.dispatch("selectSpecs",b);
                // this.aa = false;
            },
            toggle(){
                this.$store.dispatch("toggleSpec");
                this.aa = !this.aa;
                // console.log(this.aa);
            },
         },
         filters:{
             number(data){
                 let mydata = new Date(data);
                 let month = mydata.getMonth()+1;
                 let time = '';
                 if(month < 10){
                     var month1 = '0'+month;
                 }
                 let date = mydata.getDate();
                 if(date < 10){
                     var date1 = '0'+date;
                 }
                 return time = mydata.getFullYear()+'-'+month1+'-'+date+'           '+'                      ' +mydata.getHours()+':'+mydata.getMinutes()+':'+mydata.getSeconds();
             }
         }
    }
</script>

<style lang="less" scoped>
.spec{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 1.04rem;
    // z-index: 99;
    background-color: white;
    .up{
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 100%;
        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        .name{
            font-size: 0.4rem;
            margin-left: 0.36rem;
            margin-top: 0.36rem;
            width: 6rem;
            text-align: left;
            color: rgb(7, 17, 27);
        }
        .sellCount{
            margin-left: -5.4rem;
            margin-top: 0.2rem;
            font-size: 0.3rem;
        }
        .rating{
            font-size: 0.3rem;
            margin-top: -0.29rem;
            margin-left: -1.6rem;
        }
        .price{
            font-size: 0.4rem;
            margin-left: -6rem;
            margin-top: 0.3rem;
            color: rgb(240, 20, 20);
        }
        .add{
            width: 2rem;
            height: 0.7rem;
            margin-left: 5rem;
            margin-top: -1rem;
            border-radius: 1rem;
            background-color: rgb(0, 160, 220);
            p{
                font-size: 0.3rem;
                line-height: 0.7rem;
                color: rgb(255, 255, 255);
            }
        }
        .nums{
            position: absolute;
            left: 2.78rem;
            top: 7.5rem;
            z-index: -1;
        }
        .line{
            height: 0.36rem;
            width: 100%;
            margin-top: 0.55rem;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
            background-color: rgba(243, 245, 247, 1);
        }
        .tit{
            font-size: 0.4rem;
            margin-left: -5.1rem;
            margin-top: 0.36rem;
            color: #07111b;
        }
        .info{
            margin-left: 0.4rem;
            margin-top: 0.2rem;
            margin-right: 0.2rem;
            font-size: 0.3rem;
            color: rgb(77, 85, 93);
            line-height: 0.48rem;
            font-weight: 200;
        }
    }
    .middle{
        .line-one{
            height: 0.36rem;
            width: 100%;
            margin-top: 5.2rem;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
            background-color: rgba(243, 245, 247, 1);
        }
        .appraise{
            height: 1.2rem;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            display: flex;
            width: 6.5rem;
            margin-left: 0.36rem;
            margin-top: 0.36rem;
            .ss{
                width: 1.5rem;
                height: 0.8rem;
                line-height: 0.8rem;
                // border: 0.01rem solid red;
                text-align: center;
                margin-right: 0.2rem;
                border-radius: 0.1rem;
                p{
                    font-size: 0.3rem;
                }
            }
            .left{
                background-color: rgb(0, 160, 220);
            }
            .center{
                background-color: rgb(204, 236, 248);
            }
            .right{
                background-color: rgb(233, 235, 236);
            }
        }
        .selected{
            height: 1.2rem;
            border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
            width: 100%;
            text-align: center;
            .selectc{
                margin-left: -3.6rem;
                line-height: 1.2rem;
                color: rgb(183, 187, 191);
                &.q{
                    color: rgb(0, 160, 220);
                }
                i{
                    font-size: 0.6rem;
                    margin-right: 0.1rem;
                }
                span{
                    font-size: 0.3rem;
                    color: rgb(147, 153, 159);
                }
               
            }
        }
        .comments{
            width: 100%;
            height: 5rem;
            margin-top: 0.36rem;
            margin-left: 0.36rem;
            // border: 0.01rem solid cyan;
            .con{
                width: 6.7rem;
                height: 1.2rem;
                margin-bottom: 0.4rem;
                border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
                .rateTime{
                    font-size: 0.3rem;
                    color: rgb(147, 153, 159);
                    margin-left: -3.95rem;
                }
                i{
                    color: rgb(147, 153, 159);
                    margin-left: -6.4rem;
                    margin-top: 0.3rem;
                    display: block;
                    &.cc{
                        visibility: hidden;
                    }
                }
                .text{
                    font-size: 0.3rem;
                    color: #07111b;
                    margin-left: -4.1rem;
                    margin-top: -0.25rem;
                }
                .username{
                    font-size: 0.3rem;
                    color: rgb(147, 153, 159);
                    margin-top: -0.6rem;
                    margin-left: 3rem;
                }
                img{
                    border-radius: 1rem;
                    margin-right: -5.3rem;
                    margin-top: -0.5rem;
                }
            }
        }


    }

    .pass{
        position: absolute;
        top: 0.31rem;
        left: 0.3rem;
        i{
            font-size: 0.5rem;
            color: rgba(255,255,255,0.5);
        }
    }
    
}
</style>



