<template>
    <div id="app">
        <v-header></v-header>
        <detail></detail>
        <spec></spec>
        <shopping :foods="selcetFoods"></shopping>
            <div class="itany">
                <div class="itany-wrap">
                <router-link to="/goods">商品</router-link>
                </div>
                <div class="itany-wrap">
                <router-link to="/ratings">评价</router-link>
                </div>
                <div class="itany-wrap">
                <router-link to="/seller">商家</router-link>
                </div>
            </div>
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
            

            <div class="valuation" >
                <div class="icon">
                    <div class="num" v-show="totalCount" ><!-- @click="showShopping" -->
                        <p >{{totalCount}}</p>
                    </div>
                    <i class="iconfont icon-gouwuche" :class="{'cc' : totalCount > 0}" @click='hideShopping' ></i>
                </div>
                <div class="total">
                    <p>¥{{totalPrice}}</p>
                </div>
                <div class="added">
                    <p>另需配送费¥4元</p>
                </div>
                <div class="actual" :class="paytotal" @click="paytotals">
                    <p>{{pay}}</p>
                </div>
                <div class="bag"></div>
            </div>
    </div>
</template>

<script>
import header from "./components/header/header.vue";
import detail from "./components/detail/detail.vue";
import goods from "./components/goods/goods.vue";
import spec from "./components/spec/spec.vue";
import shopping from "./components/shopping/shopping.vue";


import BScroll from 'better-scroll';
import {mapGetters,mapActions} from 'vuex';

export default {
    components: {
    "v-header": header,
    detail,
    spec,
    shopping
    },
    computed:{
        ...mapGetters([
            'goods',
            'seller'
        ]),
        paytotal() {
            if (this.totalPrice < this.seller.minPrice) {
                return 'not-enough';
            } else {
                return 'enough';
            }
        },
        // 筛选所有有count的商品
        selcetFoods(){
            let  foods = [];
            this.goods.forEach( good => {
                good.foods.forEach( food => {
                    if( food.count > 0 ){
                        foods.push( food );
                    }
                })
            });
        return foods;
        },
        // 计算购物车商品的总数量
        totalCount(){
            let count = 0 ;
            this.selcetFoods.forEach( food => {
                count += food.count;
            });
            return count;
        },
        //计算购物车商品总价
        totalPrice(){
            let price = 0;
            this.selcetFoods.forEach( food => {
                price += food.count * food.price;
            });
            return price;
        },
        //结算描述
        pay(){
            if( this.totalPrice === 0 ){
                return `¥ ${this.seller.minPrice} 元起送`;
            }else if ( this.totalPrice < this.seller.minPrice ) {
                return `还差¥ ${this.seller.minPrice - this.totalPrice } 元起送`
            }else{
                return "去结算";
            }
        }
    },
    // created() {
    //     this.$store.dispatch("getGoods");
    // },
    methods:{
        ...mapActions([
            'showShopping'
        ]),
        paytotals() {
            if (this.totalPrice < this.seller.minPrice) {
                return;
            }
            window.alert('支付' + (this.totalPrice+4) + '元');
        },
        hideShopping(){
                this.$store.dispatch('hideShopping');
        },
    }
};
</script>

<style lang="less" scoped>
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    .itany {
        height: 0.8rem;
        line-height: 0.8rem;
        width: 100%;
        font-size: 0.28rem;
        color: rgb(77, 85, 93);
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.2);
        display: flex;
        .itany-wrap {
            flex: 1;
            text-align: center;
            a {
                display: block;
                &.router-link-active {
                    color: red;
                }
            }
        }
    }
    .valuation {
        position: absolute;
        display: flex;
        width: 100%;
        height: 1.04rem;
        bottom: 0;
        background-color: #141d27;
        .total{
            position: absolute;
            left: 1.45rem;
            line-height: 1.04rem;
            p{
                font-size: 0.4rem;
                color: white;
                font-weight: 700;
            }
        }
        .added{
            position: absolute;
            left: 2.5rem;
            line-height: 0.56rem;
            margin-top: 0.25rem;
            padding-left: 0.15rem;
            border-left: 0.03rem solid rgba(255, 255 ,255 ,0.1);
            p{
                font-size: 0.28rem;
                color: rgba(255, 255, 255, 0.4);
                font-weight: 700;
            }
        }
        .actual{
            position: absolute;
            right: 0.4rem;
            width: 2rem;
            margin-right: -0.4rem;
            line-height: 1.04rem;
            background-color: rgba(255, 255, 255, 0.4);
            p{
                font-size: 0.28rem;
                color: rgba(255, 255, 255, 0.5);
                font-weight: 700;
            }
            &.enough{
                background: #00b43c;
                p{
                    color: white;
                }
            }
        }
        .icon{
            position: absolute;
            width: 0.95rem;
            height: 0.95rem;
            border-radius: 1rem;
            margin-left: 0.32rem;
            display: block;
            margin-top: -0.13rem;
            color: rgba(255,255,255,0.4);
            background-color: rgba(255, 255, 255, 0.2);
            .num{
                position: absolute;
                width: 0.4rem;
                height: 0.4rem;
                left: 0.6rem;
                top: -0.15rem;
                color: white;
                background-color: red;
                border-radius: 1rem;
                p{
                    margin-top: 0.085rem;
                    margin-left: -0.01rem;
                }
            }
            i{
                margin-top: 0.26rem;
                display: block;
                font-size: 0.45rem;
                &.cc{
                    color: rgb(0, 160, 220);
                }
            }
           
        }
        .bag{
            width: 1.2rem;
            margin-left: 0.205rem;
            height: 1.2rem;
            margin-top: -0.25rem;
            border-radius: 1rem;
            background-color: #141d27;
        }
    }
}
</style>



