<template>
<div class="sellers" ref="wrapper">
<div>
        <!-- 商铺名称 -->
        <div class="name">
            <p>{{seller.name}}</p>
        </div>
        <!-- 星评 -->
        <div class="stars">
            <star class="star" :score = 'seller.score' :size = "'big'"></star>
        </div>
        <!-- 分数 -->
        <div class="score">
            <p>({{seller.score}})</p>
        </div>
        <!-- 月售 -->
        <div class="sellCount">
            <p>月售{{seller.sellCount}}单</p>
        </div>
        <!-- 喜欢 -->
        <div class="icon" :class="{'q':aa == true}" @click="icon">
            <i class="iconfont icon-xin"></i>
        </div>
        <!-- 收藏 -->
        <div class="write">
            <p>收藏</p>
        </div>
        <!-- 起送、配送、送达 -->
        <div class="info">
            <div class="infos infoed">
                <p class="top">起送价</p>
                <p class="middle">{{seller.minPrice}}</p>
                <p class="down">元</p>
            </div>
            <div class="infos">
                <p class="top">商家配送</p>
                <p class="middle">{{seller.deliveryPrice}}</p>
                <p class="down">元</p>
            </div>
            <div class="infos">
                <p class="top time">平均配送时间</p>
                <p class="middle">{{seller.deliveryTime}}</p>
                <p class="down">分钟</p>
            </div>
        </div>
        <!-- 分割线 -->
        <div class="line"></div>
        <!-- 公告 -->
        <div class="title">
            <p>公告与活动</p>
        </div>
        <!-- 公告内容 -->
        <div class="bulletin">
            <p>{{seller.bulletin}}</p>
        </div>
        <!-- 活动项 -->
        <div class="activity-item">
            <div class="imgs">
                <img src="../../../static/imgs/decrease.png" alt="">
                <p v-if="seller.supports">{{seller.supports[0].description}}</p>
            </div>
        </div>
        <div class="activity-item">
            <div class="imgs">
                <img src="../../../static/imgs/discount.png" alt="">
                <p v-if="seller.supports">{{seller.supports[1].description}}</p>
            </div>
        </div>
        <div class="activity-item">
            <div class="imgs">
                <img src="../../../static/imgs/special.png" alt="">
                <p v-if="seller.supports">{{seller.supports[2].description}}</p>
            </div>
        </div>
        <div class="activity-item">
            <div class="imgs">
                <img src="../../../static/imgs/invoice.png" alt="">
                <p v-if="seller.supports">{{seller.supports[3].description}}</p>
            </div>
        </div>
        <div class="activity-item">
            <div class="imgs">
                <img src="../../../static/imgs/guarantee.png" alt="">
                <p v-if="seller.supports">{{seller.supports[4].description}}</p>
            </div>
        </div>
        <!-- 分割线 -->
        <div class="line-two"></div>
        <!-- 商家实景 -->
        <div class="view-title">
             <p>商家实景</p>
        </div>
        <div class="view" ref='menuWrap'>
            <ul ref='picList'>
                <li v-for="(item,i) in seller.pics" :key="i"  class="pic-item"> 
                    <img :src="item" alt="">
                </li>
            </ul>
        </div>
        <!-- 分割线 -->
        <div class="line-three"></div>
        <p class="infos-title">商家信息</p>
        <div class="infos-cnt">
            <ul>
                <li v-for="(datas,e) in seller.infos" :key="e">
                    <p class="datas">{{datas}}</p>
                </li>
            </ul>
        </div>

</div>
</div>
</template>

<script>
import BScroll from 'better-scroll';
import{mapActions,mapGetters} from 'vuex';
import star from '../star/star.vue';

    export default{
        components:{
			star
		},
		computed:{ 
			...mapGetters([
				'seller',
                'detailShow'
			])
        },
        data(){
            return {aa:false};
        },
        created(){
            this.$nextTick(()=>{
                setTimeout(()=> {
                     this.menuBS();
                }, 100);
            });
        },
        watch:{
            seller(){
 				let picWidth = 256;
	          	let margin = 2;
	          	let width = (picWidth + margin) * this.seller.pics.length - margin;
	          	this.$refs.picList.style.width = width + 'px';
                if( !this.picBScroll ){
                    this.picBScroll = new BScroll( this.$refs.menuWrap , {
                    scrollX: true,
                    })
                }else{
                    this.picBScroll.refresh();
                }
 				
 			}
        },
        methods:{
            menuBS(){
                let menuBScroll = new BScroll( this.$refs.wrapper , {click:true} );
            },
            icon(){
                this.aa = !this.aa;
            }
        }
    }
</script>

<style lang="less" scoped>
.sellers{
    position: absolute;
    top: 3.54rem;
    overflow: hidden;
    z-index: -1;
    width: 90.4%;
    bottom: 1.04rem;
    padding: 0.36rem 0.36rem 0rem 0.36rem;
    // border: 0.01rem solid red;
    .name{
        margin-left: -4rem;
        p{
            font-size: 0.3rem;
            color: rgb(7, 17, 27);
        }
    }
    .stars{
        margin-left: -3rem;
        .star{
            font-size: 0.36rem;
        }
    }
    .score{
        margin-top: -0.38rem;
        margin-left: 0.7rem;
        p{
            font-size: 0.25rem;
        }
    }
    .sellCount{
        margin-left: 2.6rem;
        margin-top: -0.23rem;
        p{
            font-size: 0.25rem;
        }
    }
    .icon{
        margin-left: 6rem;
        margin-top: -1rem;
        color: rgb(77, 85, 93);
        &.q{
             color: rgb(240, 20, 20);
        }
        i{
             font-size: 0.6rem;
        }
    }
    .write{
        margin-top: 0.2rem;
        margin-left: 6rem;
        p{
            font-size: 0.25rem;
            color: rgb(77, 85, 93);
        }
    }
    .info{
        padding: 0.36rem;
        height: 1rem;
        display: flex;
        width: 6rem;
        margin-top: 0.6rem;
        overflow: hidden;
        // border: 1px solid red;
        border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
        .infos{
            flex:1;
            width: 1rem;
            height: 1.38rem;
            border-left: 0.01rem solid rgba(7, 17, 27, 0.1);
            .top{
                font-size: 0.3rem;
                color: #93999f;
                margin-left: 0.05rem;
            }
            .middle{
                font-size: 0.8rem;
                margin-top: 0.15rem;
                margin-left: -0.05rem;
            }
            .down{
                margin-top: -0.3rem;
                margin-left: 1.4rem;
            }
            .time{
                margin-left: 0.3rem;
            }
        }
        .infoed{
            margin-left: -0.5rem;
        }
    }
    .line{
        height: 0.36rem;
        width: 120%;
        margin-top: 0.3rem;
        margin-left: -1rem;
        position: absolute;
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
        border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
        background-color: rgba(7, 17, 27, 0.1);
    }
    .title{
        margin-left: -4.7rem;
        margin-top: 0.4rem;
        p{
            font-size: 0.4rem;
            margin-top: 1.08rem;
        }
    }
    .bulletin{
        margin-top: 0.3rem;
        p{
            font-size: 0.3rem;
            width: 6rem;
            left: 0.3rem;
            position: relative;
            text-align: left;
            line-height: 0.48rem;
            margin-bottom: 0.5rem;
            color: #f01414;
        }
    }
    .activity-item{
        border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
        height: 1.4rem;
        img{
            margin-top: 0.45rem;
            margin-left: -5.8rem;
        }
        p{
            font-size: 0.3rem;
            width: 5rem;
            text-align: left;
            margin-top: -0.52rem;
            margin-left: 1rem;
        }
    }
    .line-two{
        height: 0.36rem;
        width: 120%;
        margin-left: -1rem;
        top: 15.75rem;
        position: absolute;
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
        border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
        background-color: rgba(7, 17, 27, 0.1);
    }
    .view-title{
        p{
            font-size: 0.4rem;
            margin-left: -5.2rem;
            margin-top: 1rem;
        }
    }
    .view{
        width: 100%;
        // border: 1px solid red;
        overflow: hidden;
        box-sizing: border-box;
        white-space: nowrap;
        margin-top: 0.4rem;
        margin-left: 0.349rem;
        .pic-item{
            display: inline-block;
            margin-right: 2.3rem;
            width: 2.4rem;
            height: 1.8rem;
            img{
                width: 4.5rem;
                height: 3rem;
                margin-left: -1.095rem;
            }
            &:last-child{
                margin: 0;
            }
        }
    }
    .line-three{
        height: 0.36rem;
        width: 120%;
        top: 21rem;
        margin-left: -1rem;
        position: absolute;
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
        border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
        background-color: rgba(7, 17, 27, 0.1);
    }
    .infos-title{
        font-size: 0.4rem;
        margin-left: -5.2rem;
        margin-top: 1.35rem;
    }
    .infos-cnt{
        width: 100%;
        height:7rem;
        margin-top: 0.35rem;
        overflow: hidden;
        text-align: left;
        margin-left: 0.2rem;
        // border: 0.01rem solid cyan;
        p{
            font-size: 0.23rem;
            width: 6.5rem;
            height: 1.6rem;
            border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
            line-height: 1.6rem;
        }
    }

}
</style>



