<template>
<transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
    <div class="detail" v-show="detailShow">
        <div class="name">
            <p>{{seller.name}}</p>
            <star :score = 'seller.score' :size = "'big'"></star>
        </div>
        <!-- 优惠信息 -->
        <div class="line">
            <div class="l-line"> 
            </div>
            <div class="info">
                <p>优惠信息</p>
            </div>
            <div class="r-line"> 
            </div>
        </div>
        <!-- 减 -->
        <div class="decrease">
            <img src="../../../static/imgs/decrease.png" alt="">
        </div>
        <div class="descriptiono">
            <p v-if="seller.supports">{{seller.supports[0].description}}</p>
        </div>
        <!-- 折 -->
        <div class="discount">
            <img src="../../../static/imgs/discount.png" alt="">
        </div>
        <div class="descriptiont">
            <p v-if="seller.supports">{{seller.supports[1].description}}</p>
        </div>
        <!-- 特 -->
        <div class="special">
            <img src="../../../static/imgs/special.png" alt="">
        </div>
        <div class="descriptions">
            <p v-if="seller.supports">{{seller.supports[2].description}}</p>
        </div>
        <!-- 商家公告 -->
        <div class="lines">
            <div class="l-lines"> 
            </div>
            <div class="infos">
                <p>商家公告</p>
            </div>
            <div class="r-lines"> 
            </div>
        </div>
        <!-- 详情 -->
        <div class="bulletin">
            <p>{{seller.bulletin}}</p>
        </div>
        <div class="pass">
            <i @click='hideDetail' class="iconfont icon-X"></i>
        </div>
    </div>
</transition>
</template>

<script>
    import { mapGetters } from 'vuex';
    import animate from 'animate.css';
    import star from '../star/star.vue';
     export default{
        components:{
			star
		},
		computed:{ 
			...mapGetters([
				'seller',
				'detailShow'
			])
		},
         methods:{
             hideDetail(){
                 this.$store.dispatch('hideDetail');
             }
         }
     }
</script>

<style lang="less" scoped>
    .detail{
		font-size: 0.4rem;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
        bottom: 0;
        z-index: 99;
        background-color: rgba(7,17,27,0.6);
        .name{
            position: absolute;
            top: 1.28rem;
            left: 25%;
            p{
                font-size: 0.43rem;
                font-weight: bold;
                color: rgb(255,255,255);
                line-height: 0.32rem;
            }
        }
        .line{
            position: absolute;
            position: fixed;
            top:2.97rem;
            width: 100%;
            // border: 0.02rem solid rgba(255, 255, 255, 0.2);
            .l-line{
                position: absolute;
                top:0.2rem;
                left: 0.72rem;
                width: 2rem;
                line-height: 0.2638rem;
                border: 0.02rem solid rgba(255, 255, 255, 0.2);
            }
            .info{
                color: rgb(255,255,255);
                font-size: 0.28rem;
                font-weight: 7rem;
                line-height: 28px; 
                p{
                    font-size: 0.35rem;
                }
            }
            .r-line{
                position: absolute;
                top:0.2rem;
                right: 0.72rem;
                width: 2rem;
                line-height: 0.2638rem;
                border: 0.02rem solid rgba(255, 255, 255, 0.2);
            }
        }
        .decrease{
            position: absolute;
            top:3.76rem;
            left:0.92rem;
            img{
                width: 0.32rem;
                height: 0.32rem;
            }
        }
        .descriptiono{
                position: absolute;
                top:3.83rem;
                left:1.38rem;
                p{
                    font-size: 0.27rem;
                    font-weight: 2rem;
                    color: rgb(255,255,255);
                    line-height: 0.24rem;
                }
        }
        .discount{
            position: absolute;
            top:4.24rem;
            left:0.92rem;
            img{
                width: 0.32rem;
                height: 0.32rem;
            }
        }
        .descriptiont{
                position: absolute;
                top:4.31rem;
                left:1.38rem;
                p{
                    font-size: 0.27rem;
                    font-weight: 2rem;
                    color: rgb(255,255,255);
                    line-height: 0.24rem;
                }
        }
        .special{
            position: absolute;
            top:4.74rem;
            left:0.92rem;
            img{
                width: 0.32rem;
                height: 0.32rem;
            }
        }
        .descriptions{
            position: absolute;
            top:4.8rem;
            left:1.38rem;
            p{
                font-size: 0.27rem;
                font-weight: 2rem;
                color: rgb(255,255,255);
                line-height: 0.24rem;
            }
        }
        .lines{
            position: absolute;
            position: fixed;
            top:6.75rem;
            width: 100%;
            // border: 0.02rem solid rgba(255, 255, 255, 0.2);
            .l-lines{
                position: absolute;
                top:0.2rem;
                left: 0.72rem;
                width: 2rem;
                line-height: 0.2638rem;
                border: 0.02rem solid rgba(255, 255, 255, 0.2);
            }
            .infos{
                color: rgb(255,255,255);
                font-size: 0.28rem;
                font-weight: 7rem;
                line-height: 28px;
                p{
                    font-size: 0.35rem;
                } 
            }
            .r-lines{
                position: absolute;
                top:0.2rem;
                right: 0.72rem;
                width: 2rem;
                line-height: 0.2638rem;
                border: 0.02rem solid rgba(255, 255, 255, 0.2);
            }
        }
        .bulletin{
            position: absolute;
            top: 7.23rem;
            left:0.90rem;
            right: 0.90rem;
            p{
                color: rgb(255,255,255);
                font-size: 0.259rem;
                margin-top: 0.2rem;
                line-height: 0.48rem; 
            }
        }
        .pass{
            position: absolute;
            bottom: 0.64rem;
            position: fixed;
            width: 100%;
            i{
                font-size: 0.4rem;
                color: rgba(255,255,255,0.5);
            }
        }
        
	}
</style>




