<template>
  <div class="header">
      <!-- 背景图 -->
      <div class="bg">
          <img :src="seller.avatar" alt="">
      </div>
       <!-- 图标 -->
      <div class="avatar">
          <img :src="seller.avatar" alt="" @click="showDetail">
      </div>
       <!-- 品牌 -->
      <div class="brand">
          <img src="../../../static/imgs/brand.png" alt="">
      </div>
       <!-- 蜂鸟 -->
      <div class="description">
          <p>{{seller.description}}</p>
      </div>
      <!-- 减 -->
      <div class="decrease">
          <img src="../../../static/imgs/decrease.png" alt="">
      </div>
      <div class="supports">
          <p v-if='seller.supports'>{{seller.supports[0].description}}</p>
      </div>
       <!-- 店名 -->
      <div class="name">
           <p>{{seller.name}}</p>
      </div>
      <!-- 5个 -->
      <div class="num">
          <p v-if='seller.supports'>{{seller.supports.length}}个 ></p>
      </div>
      
      <div class="information">
            <!-- 公告 -->
            <div class="bulletin">
                <img src="../../../static/imgs/bulletin.png" alt="">
            </div>
            <!-- 公告信息 -->
            <div class="bulletins">
                <p>{{seller.bulletin}}</p>
            </div>
            <!-- 结尾 -->
            <div class="point">
                <p>...</p>
            </div>
            <div class="more">
                <p>></p>
            </div>
      </div>

 
  </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex';
    export default{
        computed:{
            ...mapGetters([
				'seller'
			])
        },
        methods:{
            ...mapActions([
                'showDetail'
            ])
        },
        created() {
            this.$store.dispatch("getSeller");
        },
    }
</script>

<style lang="less" scoped>
    .header {
		width: 100%;
        height: 2rem;
        padding: 0.48rem 0.24rem 0.24rem 0.48rem;
        position: relative;
        overflow: hidden;
            .bg{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: -1;
                img{
                    width: 100%;
                    height: 100%;
                    filter:blur(6px);
                }
            }
            .avatar{
                position: absolute;
                img{
                    width: 1.28rem;
                    height: 1.28rem;
                    border-radius: 0.04rem;
                }
            }
            .brand{
                position: absolute;
                top: 0.52rem;
                left: 2.08rem;
                img{
                    width: 0.6rem;
                    height: 0.36rem;
                }
            }
            .description{
                position: absolute;
                top: 1.04rem;
                left: 2.08rem;
                p{
                    font-size: 0.24rem;
                    color:rgb(255, 255, 255);
                    font-weight: 2rem;
                    line-height: 0.24rem;
                }
            }
            .decrease{
                position: absolute;
                top: 1.37rem;
                left: 2.08rem;
                img{
                    width: 0.32rem;
                    height: 0.32rem;
                }
            }
            .supports{
                position: absolute;
                top: 1.43rem;
                left: 2.48rem;
                 p{
                    font-size: 0.2rem;
                    color: rgb(255, 255, 255);
                    font-weight: 2rem;
                    line-height: 0.24rem;
                }
            } 
		    .name{  
                position: absolute;
                top: 0.52rem;
                left: 2.80rem;
                p{
                    color:rgb(255, 255, 255);
                    font-weight: bold;
                    line-height: 0.36rem;
                    font-size: 0.32rem;
                }
            }
            .num{
                position: absolute;
                top: 1.43rem;
                right: 0.9rem;
                height: 0.48rem;
                width: 0.8rem;
                background-color: rgba(7,17,27,0.4);
                border-radius: 0.25rem;
                p{
                    position: relative;
                    top: 0.14rem;
                    left: 0.04rem;
                    color:rgb(255, 255, 255);
                    font-weight: 2rem;
                    line-height: 0.24rem;
                    font-size: 0.20rem;
                }
            }

            .information{
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 0.56rem;
                background-color: rgba(7,17,27,0.4);
                opacity:0.7;
                overflow: hidden;
                .bulletin{
                    img{
                        position: absolute;
                        left: 0.17rem;
                        width: 0.5rem;
                        height: 0.3rem;
                        top: 0.12rem;
                    }
                }
                .bulletins{
                    position: absolute;
                    bottom: 0;
                    left: 0.76rem;
                    width: 75%;
                    height: 0.56rem;
                        p{
                            font-size: 0.2rem;
                            color: rgb(255, 255, 255);
                            font-weight: 2rem;
                            line-height: 0.56rem;
                        }
                }
                .point{
                    position: absolute;
                    bottom: 0.17rem;
                    right: 1.1rem;
                    p{
                        color: rgb(255, 255, 255);
                    }
                }
                .more{
                    position: absolute;
                    bottom: 0.17rem;
                    right: 0.8rem;
                    p{
                        color: rgb(255, 255, 255);
                    }
                }
            }
           
	}
    
</style>



