<template>
<div class="ratings" ref="wrapper">
<div>
    <!-- 星级评分展示区 -->
    <div class="ratings-content">
        <div class="overview">
            <div class="overview-left">
                <h1 class="score">{{seller.score}}</h1>
                <div class="title">综合评分</div>
                <div class="rank">高于周边商家{{seller.rankRate}}%</div>
            </div>
            <div class="overview-right">
                <div class="score-wrapper">
                    <span class="title">服务态度</span>
                        <star class="star" :score = 'seller.serviceScore' :size = "'middles'"></star>
                    <span class="score">{{seller.serviceScore}}</span>
                </div>
                <div class="score-wrapper">
                    <span class="title">商品评分</span>
                        <star class="star" :score = 'seller.foodScore' :size = "'middles'"></star>
                    <span class="score">{{seller.foodScore}}</span>
                </div>
                <div class="delivery-wrapper">
                    <span class="title">送达时间</span>
                    <span class="delivery">{{seller.deliveryTime}}分钟</span>
                </div>
            </div>
        </div>
    </div>
    <!-- 分割线 -->
    <div class="line"></div>
    <!-- 满意度 -->
    <div class="appraise" ><!-- v-for="(item,i) in ratings" :key="i" -->
        <div class="ss left">
            <p @click="select(2)">{{types[2]}}({{ratings.length}})</p>
        </div>
        <div class="ss center">
            <p @click="select(0)">{{types[0]}}({{goodCount}})</p>
        </div>
        <div class="ss right">
            <p @click="select(1)">{{types[1]}}({{badCount}})</p>
        </div>
    </div>
    <div class="selected">
        <div class="selectc" :class="{'q':aa == true}" @click="toggle">
            <i class="iconfont icon-duihao"></i>
            <span >只看有内容的评价</span>
        </div>
    </div>
    <div class="rating">
        <ul >
            <li v-for="(item,i) in selectRatings" :key="i">
                <div class="cnt">
                    <img :src="item.avatar" alt="">
                    <span class="username">{{item.username}}</span>
                    <star class="star" :score = 'item.score' :size = "'middles'"></star>
                    <span class="deliveryTime" v-if="item.deliveryTime">{{item.deliveryTime}}分钟送达</span>
                    <div class="text-cnt">
                        <p class="text">{{item.text}}</p>
                        <i v-if="item.text && item.score>2" class="iconfont icon-damuzhion"></i>
                        <ul>
                            <li v-for="(items,i) in item.recommend" :key="i" class="recommend-cnt">
                                <p class="recommend">{{items}}</p>
                            </li>
                        </ul>
                        
                    </div>
                </div>
            </li>
        </ul>
    </div>


</div>
</div>
</template>

<script>
import BScroll from 'better-scroll';
import { mapActions, mapGetters } from "vuex";
import star from "../star/star.vue";

        export default {
            components: {
                star
            },
            data(){
                return {aa:false};
            },
            computed: {
                ...mapGetters([
                    "seller",
                    "ratings",
                    "selectRatings",
                    "types"
                ]),
                goodCount(){
                    return this.ratings.filter( rating => {
                        return rating.rateType === 0; 
                    }).length;
                },
                badCount(){
                    return this.ratings.filter( rating => {
                        return rating.rateType === 1; 
                    }).length;
                }
            },
            created(){
                this.$store.dispatch('getRatings');
                this.$store.dispatch('change',['满意','不满意','全部']);
            },
            watch:{
                selectRatings(){
                    this.$nextTick(()=>{
                        if(!this.menuBScroll){
                            this.menuBScroll = new BScroll( this.$refs.wrapper , {click:true} );
                        }else{
                            this.menuBScroll.refresh();
                        }
                    });
                }
            },
            methods:{
                // menuBS(){
                //     let menuBScroll = new BScroll( this.$refs.wrapper , {click:true} );
                // },
                select(c){
                    this.$store.dispatch("select",c);
                    this.aa = false;
                },
                toggle(){
                    this.$store.dispatch("toggle");
                    this.aa = !this.aa;
                    // console.log(this.aa);
                },
            }
        };
</script>

<style lang="less" scoped>
.ratings {
    position: absolute;
    top: 3.55rem;
    bottom: 1.04rem;
    z-index: -1;
    width: 100%;
    overflow: hidden;
    .overview {
        display: flex;
        padding: 0.18rem 0 0.18rem 0.18rem;
        .overview-left {
            padding: 0.06rem 0rem;
            border-right: 0.01rem solid rgba(7, 17, 27, 0.1);
            text-align: center;
            flex: 0 0 2.2rem;
            .score {
                line-height: 0.28rem;
                font-size: 0.6rem;
                margin-top: 0.4rem;
                color: rgb(255, 153, 0);
            }
            .title {
                font-size: 0.3rem;
                margin-top: 0.4rem;
                color: rgb(7, 17, 27);
            }
            .rank {
                line-height: 0.1rem;
                font-size: 0.2rem;
                margin-top: 0.3rem;
                color: rgb(147, 153, 159);
            }
        }
        .overview-right {
            flex: 1;
            padding: 0.06rem 0 0.06rem 0.24rem;
            padding-left: 0.06rem;
            .score-wrapper {
                line-height: 0.18rem;
                margin-top: 0.08rem;
                font-size: 0.3rem;
                .title {
                    margin-left: -3rem;
                    margin-top: 0.4rem;
                    display: block;
                    margin-bottom: 0.05rem;
                    line-height: 0.18rem;
                    color: rgb(7, 17, 27);
                }
                .star {
                    margin-top: -0.47rem;
                    margin-left: 0.9rem;
                }
                .score {
                    margin-left: 3.6rem;
                    margin-top: -0.2rem;
                    display: block;
                    color: rgb(266, 153, 0);
                }
            }
            .delivery-wrapper {
                font-size: 0.3rem;
                .title {
                    margin-left: -3rem;
                    margin-top: 0.4rem;
                    display: block;
                    margin-bottom: 0.05rem;
                    line-height: 0.18rem;
                    color: rgb(7, 17, 27);
                }
                .delivery {
                    margin-left: -0.17rem;
                    margin-top: -0.265rem;
                    display: block;
                    color: rgb(147, 153, 159);
                }
            }
        }
    }
    .line{
        height: 0.36rem;
        width: 100%;
        margin-top: 0.3rem;
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
        border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
        background-color: rgba(243, 245, 247, 1);
    }
    .appraise{
        height: 1.2rem;
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
        display: flex;
        width: 6.5rem;
        margin-left: 0.36rem;
        margin-top: 0.36rem;
        .ss{
            width: 1.5rem;
            height: 0.8rem;
            line-height: 0.8rem;
            // border: 0.01rem solid red;
            text-align: center;
            margin-right: 0.2rem;
            border-radius: 0.1rem;
            p{
                font-size: 0.3rem;
            }
        }
        .left{
            background-color: rgb(0, 160, 220);
        }
        .center{
            background-color: rgb(204, 236, 248);
        }
        .right{
            background-color: rgb(233, 235, 236);
        }
    }
    .selected{
        height: 1.2rem;
        border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
        width: 100%;
        text-align: center;
        .selectc{
            margin-left: -3.6rem;
            line-height: 1.2rem;
            color: rgb(183, 187, 191);
            &.q{
                color: rgb(0, 160, 220);
            }
            i{
                font-size: 0.6rem;
                margin-right: 0.1rem;
            }
            span{
                font-size: 0.3rem;
                color: rgb(147, 153, 159);
            }
        }
        
    }
    .rating{
        .cnt{
            width: 6.75rem;
            height: 3rem;
            margin-top: 0.36rem;
            margin-left: 0.36rem;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            img{
                border-radius: 1rem;
                margin-left: -5.85rem;
            }
            .username{
                font-size: 0.3rem;
                margin-left: -3.3rem;
                margin-top: -0.8rem;
                display: block;
            }
            .star{
                font-size: 0.4rem;
                margin-left: -2.5rem;
                margin-top: -0.3rem;
            }
            .deliveryTime{
                color: #93999f;
                margin-left: 0.9rem;
                margin-top: -0.3rem;
                display: block;
            }
            .text-cnt{
                width: 5.6rem;
                height: 1.75rem;
                margin-top: 0.2rem;
                margin-left: 1.15rem;
                text-align: left;
                // border: 0.01rem solid red;
                .text{
                    font-size: 0.3rem;
                    font-weight: 200;
                    color: rgb(7, 17, 27);
                    line-height: 0.36rem;
                }
                i{
                    margin-top: 0.2rem;
                    display: block;
                }
                .recommend-cnt{
                    display: inline-block;
                    margin-left: 0.5rem;
                    margin-bottom: 0.15rem;
                    border: 0.01rem solid rgb(147, 153, 159);
                    position: relative;
                    top: -0.23rem;
                    padding: 0.04rem;
                    // .recommend{
                    //     margin-right: 0.14rem;
                    // }
                }
            }
        }
    }

}
</style>



