<template>
<transition enter-active-class="animated bounceInRight" leave-active-class="animated bounceOutLeft">
<div class="wrap" v-show="showShopping" ref="wrapper">
        <div class="mohu"></div>
        <div class="shopping">
            <div class="cart">
                <span class="shopcart">购物车</span>
                <span class="pass" @click="empty">清空</span>
            </div>
            <div class="foods" v-for="(item,i) in foods" :key="i">
                <div class="foodName">{{item.name}}</div>
                <div class="price">¥ {{item.price * item.count}}</div>
                <div class="number">
                    <numbers :food ="item" class="number-food"></numbers>
                </div>
            </div>
        </div>
</div>
</transition>
</template>

<script>
import BScroll from 'better-scroll';
import {mapGetters,mapActions} from 'vuex';
import animate from 'animate.css';
import numbers from '../numbers/numbers.vue';

    export default{
        props:['foods','totalprice'],
        computed:{ 
			...mapGetters([
                'showShopping',
                
			])
        },
        components:{
            numbers
        },
        created(){
            // this.$store.dispatch("getGoods");
            this.$nextTick(()=>{
                setTimeout(()=> {
                     this.menuBS();
                }, 100);
               
            });
        },
        // watch:{
        //     goods(){
        //         this.$nextTick(()=>{
        //             this.menuBS();
        //         });
        //     }
        // },
        methods:{
            
            menuBS(){
                let menuBScroll = new BScroll( this.$refs.wrapper, {click:true} );
            },
            empty() {
                this.foods.forEach((food) => {
                    food.count = 0;
                });
            },
            
         }
    }
</script>

<style lang="less" scoped>
.wrap{
    position: absolute;
    top: 0;left: 0;right: 0;bottom: 0;
    width: 100%;
    height: 11.4rem;
    .mohu{
        background: rgba(7,17,27,0.6);
        height: 12.2rem;
    }
    .shopping{
        width: 100%;
        background: #fff;
        position: absolute;
        bottom: -1rem;
        .cart{
            display: flex;
            justify-content: space-between;
            line-height: 1rem;
            background: #f3f5f7;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            span{
                display: inline-block;
            }
            .shopcart{
                font-size: 0.36rem;
                color: rgb(7, 17, 27);
                padding-left: 0.36rem;
            }
            .pass{
                font-size: 0.32rem;
                color: rgb(0,160,220);
                padding-right: 0.36rem;
            }
        }
        .foods{
            font-size: 0.36rem;
            color: #07111b;
            height: 0.96rem;
            line-height: 0.96rem;
            padding-left: 0.36rem;
            // border: 0.01rem solid red;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            display: flex;
            .foodName{
                    width: 4rem;
                    // border: 0.01rem solid cyan;
                    text-align: left;
                    overflow: hidden;
                    height: 0.96rem;
            }
            .price{
                    color: tomato;
                    width: 1.2rem;
                    height: 0.96rem;
                    line-height: 0.96rem;
                    // border: 0.01rem solid blue;
            }
            .number{
                    width: 1.864rem;
                    height: 0.96rem;
                    line-height: 0.96rem;
                    // border: 0.01rem solid;
                    .number-food{
                            position: relative;
                            left: -2.26rem;
                            top: -1.14rem;
                    }
            }
            
        }
    }
}
</style>



