<template>
<div >
    <div class="lined">
        <i v-show="food.count > 0" class="iconfont icon-jianhao" @click="reduce"></i>
    </div>
    <div class="nums">
        <p v-show="food.count > 0">{{food.count}}</p>
    </div>
    <div class="lineu">
        <i class="iconfont icon-jiahao" @click="add"></i>
    </div>
</div>
</template>

<script>
import Vue from 'vue';
    export default{
        props:['food'],
        methods:{
            add(){
                if( !this.food.count ){
                    Vue.set( this.food , 'count' , 1);
				}else{
                    this.food.count ++;
				}
            },
            reduce(){
                if(this.food.count){
                    this.food.count --;
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .lineu{
        position: absolute;
        left: 3.6rem;
        top: 1.15rem;
        i{
            color: rgb(0, 160, 220);
            font-size: 0.35rem;
        }
    }
    .lined{
        position: absolute;
        left: 2.5rem;
        top: 1.15rem;
        i{
        color: rgb(0, 160, 220);
        font-size: 0.327rem;
        }
    }
    .nums{
        position: absolute;
        left: 3.13rem;
        top: 1.2rem;
        p{
            font-size: 0.25rem;
        }
    }
</style>



